import React, { FC } from "react";
import "./Works.css";
import adminPanelImg from "../../img/worksImg/admin-panel.png";
import XOimg1 from "../../img/worksImg/XO-img1.png";
import XOimg2 from "../../img/worksImg/XO-img2.png";

interface IWorkItem {
  imgs: string[];
  title: string;
  description?: string;
  urlView?: string;
}

const worksItems: IWorkItem[] = [
  {
    imgs: [adminPanelImg],
    title: "Админ-панель",
    description:
      "Админ-панель для добавление нового товара в магазин написан на React",
  },
  {
    imgs: [XOimg1, XOimg2],
    title: "Крестики нолики",
    description: "Крестики нолики написаные на чистом JS + JQuery",
    urlView: "http://p127.fun/start.html",
  },
];

const WorkItem: FC<IWorkItem> = (props) => {
  return (
    <div className="work-item" data-aos="fade-up">
      <div className="title">
        <h1>{props.title}</h1>
      </div>
      <div className="description">
        <p>{props.description || ""}</p>
      </div>
      <div className="img-container">
        {props.imgs.map((el, i) => (
          <img src={el} alt={el} key={i} />
        ))}
      </div>
      <div className="view">
        {props.urlView ? (
          <a href={props.urlView || "/"} target="_blank">
            {props.urlView ? "Посмотреть" : ""}
          </a>
        ) : (
          ""
        )}
      </div>
    </div>
  );
};

const Works: FC = () => {
  return (
    <div className="works">
      <h1 data-aos="fade-down">МОИ РАБОТЫ</h1>
      <div className="works-container">
        {worksItems.map((el, i) => (
          <WorkItem
            imgs={el.imgs}
            title={el.title}
            description={el.description}
            urlView={el.urlView}
            key={i}
          />
        ))}
      </div>
    </div>
  );
};

export default Works;
