import React, { FC } from "react";
import "./Main.css";
import logo from "../../img/logo-light.png";

const Main: FC = () => {
  return (
    <div className="main">
      <div className="main-content">
        <div className="logo" data-aos='zoom-in'>
          <img src={logo} alt="Logo [VP]" />
        </div>
        <div className="text">
          <h3 className="-slide" data-aos='fade-down'>
            Hello World, меня зовут Попович Валерий и я{" "}
          </h3>
          <h1 className="-slide" data-aos='fade-down'>JUNIOR FRONT-END РАЗРОБОТЧИК</h1>
          <h3 className="-slide" data-aos='fade-down'>веб-сайтов и веб-приложений</h3>
        </div>
      </div>
    </div>
  );
};

export default Main;
