import React from "react";
import { FC } from "react";
import "./Skills.css";

interface ISkillsItem {
  title: string;
  progres: number;
}

const SkillsItem: FC<ISkillsItem> = (props) => {
  const marginProgress = 4;
  return (
    <div className="skills-item">
      <h2 data-aos="fade-right">{props.title}</h2>
      <div className="progerss-bar">
        <div className="progress-container">
          <div
            className={`progerss-item ${
              props.progres >= marginProgress ? "done" : ""
            }`}
          >
            Beginner
          </div>
          <div className={`progerss-item ${props.progres >= 34 ? "done" : ""}`}>
            Junior
          </div>
          <div className={`progerss-item ${props.progres >= 67 ? "done" : ""}`}>
            Middle
          </div>
          <div className={`progerss-item ${props.progres >= 97 ? "done" : ""}`}>
            Senior
          </div>
        </div>
        <div className="progress-container">
          <div className="progress-line">
            <div
              className="progress-line-res"
              data-aos="zoom-in"
              data-aos-delay="300"
              style={{ width: `${props.progres}%` }}
            >
              {" "}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const skillsData: ISkillsItem[] = [
  { title: "JS", progres: 37 },
  { title: "HTML CSS", progres: 40 },
  { title: "React", progres: 35 },
  { title: "Vue.js", progres: 17 },
];

const Skills: FC = () => {
  return (
    <div className="skills">
      <h1 data-aos="fade-down">МОИ НАВЫКИ И ЗНАНИЯ</h1>
      {skillsData.map((el, i) => (
        <SkillsItem title={el.title} progres={el.progres} key={i} />
      ))}
    </div>
  );
};

export default Skills;
